@import "src/common/styles/sass/variables";

@mixin text($fontFamily, $fontSize, $fontWeight, $lineHeight, $letterSpacing, $color) {
  font-family: $fontFamily;
  font-size: $fontSize;
  font-weight: $fontWeight;
  line-height: $lineHeight;
  letter-spacing: $letterSpacing;
  color: $color;
}

@mixin flexMain ($display, $flexDirection, $justifyContent, $alignItems) {
  display: $display;
  flex-direction: $flexDirection;
  justify-content: $justifyContent;
  align-items: $alignItems;
}

@mixin tab_animation ($opacity, $transform) {
  opacity: $opacity;
  transform: $transform;
}

@mixin animate($keyframesName,$duration,$timing-function,$iteration-count,$delay,$direction,$fill-mode,$play-state){
  animation: $keyframesName $duration $timing-function $iteration-count $delay $direction $fill-mode $play-state;
}

@mixin keyframes($name){
  @keyframes #{$name}{
    @content;
  }
}

@mixin container () {
  width: 80%;
  margin: 0 auto;
}

@mixin bgSettings() {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}